@import"https://fonts.googleapis.com/css2?family=Roboto:wght@200;300;400&display=swap";@import"https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400&display=swap";@import"https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css";.flex{display:flex;flex-direction:row;align-items:center;justify-content:center}ul{list-style-type:none}a:-webkit-any-link{text-decoration:none}a:-webkit-any-link{text-decoration:none}button,input[type=submit],input[type=reset]{background:none;color:inherit;border:none;padding:0;font:inherit;cursor:pointer;outline:inherit}.body{padding-right:5%;padding-left:3%;height:fit-content}.grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(15vw, 1fr));column-gap:1.5rem;row-gap:1.5rem;padding-left:-1.5%}.item{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;box-shadow:rgba(3,8,20,.1) 0px .15rem .5rem,rgba(2,8,20,.1) 0px .075rem .175rem;height:100%;width:100%;border-radius:1vw;transition:all 200ms;overflow:hidden;background-size:cover;background-position:center;background-repeat:no-repeat;border:.005vw solid #ccc}.item:hover{margin:-0.4vw;-webkit-box-shadow:7px 7px 10px -9px #ccc;box-shadow:14px 14px 14px -7px #ccc}.placeImg{border-radius:.5vw;max-width:90%;max-height:90%;margin:.5vw;-webkit-box-shadow:7px 7px 10px -9px;box-shadow:7px 7px 2px -9px #ccc}.item:hover .placeImg{transform:scale(1.04)}.name{font-size:1.5vw;letter-spacing:.06vw;color:#212427;font-family:"Oswald",Sans-Serif;font-weight:normal;text-shadow:0px 2px 2px rgba(255,255,255,0);padding-left:1vw;text-align:left}.city{color:#212427;letter-spacing:.02vw;font-size:.9vw;text-align:left;padding-left:1vw;font-family:"Quattrocento",Serif;text-shadow:0px 2px 2px rgba(255,255,255,0)}.ranking{color:gray;font-weight:100;font-size:1.2vw;font-family:"Quattrocento",Serif;padding-right:1vw}.title{display:flex;justify-content:space-between;overflow:hidden;align-items:center;margin-top:-1.5vw;margin-bottom:-2vw}.topBar{justify-content:space-evenly}.to-top{color:#161b21;padding-top:6vw;display:inline-block;position:relative;border-color:#161b21;text-decoration:none;transition:all .3s ease-out;font-size:3vw}.to-top:before{content:"▲";font-size:2vw;position:absolute;top:0;left:50%;margin-left:-2.5vw;border:solid .13em #161b21;border-radius:10vw;width:4vw;height:4vw;border-color:inherit;transition:transform .5s ease-in}.to-top:hover{color:#f4a950;border-color:#f4a950}.to-top:hover:before{transform:rotate(360deg)}@media(max-width: 992px){.grid{grid-template-columns:repeat(auto-fill, minmax(20vw, 1fr))}.placeImg{border-radius:1vw}.item{border-radius:2vw}.name{font-size:2vw}.city{font-size:1.5vw}.ranking{font-size:2.5vw}}@media(max-width: 768px){.grid{grid-template-columns:repeat(auto-fill, minmax(28vw, 1fr))}}